.time-calc{
    width: 392px;
    padding-bottom: 36px;
    margin-left: 313px;
    &__title{
        font-weight: 400;
        font-size: 16px;
        margin-bottom: 36px;
        margin-left: 151px;
    }
    &__list{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 392px;
        height: 35px;
    }
    &__indentation{
        stroke-width: 1px;
        stroke: #000;
    }
    &__field{
        display: flex;
        width: 150px;
        height: 35px;
        box-shadow: 3px 3px 0 0 rgba(0, 0, 0, 0.25);
        background-color: #000;
        border-radius: 20px;
    }
    &__input{
        border: 0;
        padding: 10px;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
        width: 115px;
        height: 35px;
        background: #d7d7d7;
        font-weight: 400;
        font-size: 12px;
        color: #7e7e7e;
    }
    &__but{
        display: block;
        background-color: unset;
        border: 0;
        margin: 10px;
    }
    &__result{
        font-weight: 400;
        font-size: 12px;
    }
}